














import { Component, Vue } from 'vue-property-decorator';
import Logout from '@/components/Logout.vue';

@Component({
    components: {
        Logout
    }
})
export default class Navbar extends Vue {
    done = false;
    user = {
        id : null,
        username : ""
    };


    created() {
        this.axios.get("/api/v1/session", {withCredentials: true}).then((response)=>{
            this.user.id = response.data.user_id;
            if (this.user.id) {
                this.axios.get(`/api/v1/users/${this.user.id}.json`, {withCredentials: true}).then((response)=>{
                    this.user.username = response.data.username;
                    this.done = true;
                })
            } else {
                this.done = true;
            }
        })
    }
}
